import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpService } from '../../../providers/http.service';
import { ToastService } from '../../../providers/toast.service';
import { UtilService } from '../../../providers/util.service';
import { AppConfig } from '../../../appConfig';
import { NzModalService } from 'ng-zorro-antd';
@Component({
  selector: 'app-mymatch',
  templateUrl: './mymatch.component.html',
  styleUrls: ['./mymatch.component.css']
})
export class MymatchComponent implements OnInit {


  userInfo: any;
  myMatch_list = [];
  //折叠面板样式
  customStyle = {
    border: '0px'
  }

  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
    private modalService: NzModalService,

  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.getMyGame();
  }
  //判断是否有登录，是否有userinfo
  checkLogin() {
    this.userInfo = this.utilService.getLS("userInfo");
    if (!this.userInfo) {
      this.toastService.showMessage("info", "请您先登录");
      this.router.navigate(['/home']);
      this.utilService.LoginIsVisible = true;
    }
  }

  getMyGame(pagenow = 1) {
    this.checkLogin();
    var data = {
      user_id: this.userInfo.id,
    }

    this.httpService.post('getMyMatch', data, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        this.myMatch_list = data;

      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  goMatchDetail(id) {
    let navigationExtras: NavigationExtras = {
      queryParams: { 'match_id': id },
      // fragment: 'anchor'
    };
    this.router.navigate(['/matchdetail'], navigationExtras);
  }

  //退赛申请
  returnMatch(enlist_id) {
    console.log(enlist_id);
    this.modalService.confirm({
      nzTitle: '<i>确定退赛吗?</i>',
      nzContent: '<b>退赛后如想再参赛需联系您所在机构的管理员</b>',
      nzOnOk: () => {
        var data = {
          enlist_id: enlist_id
        }
        this.httpService.post('returnMatch', data, (res) => {
          console.log(res);
          if (res.statusCode == 0) {
            this.toastService.showMessage("info", res.msg);;
            this.getMyGame();
          }
          else {
            this.toastService.showMessage("error", res.msg);
          }
        });
      }
    });
  }

  modifyGroup(item) {
    console.log(item);
    this.modalService.confirm({
      nzTitle: '<i>确定更换到 ' + item.group_name + ' 吗?</i>',
      nzContent: '',
      nzOnOk: () => {
        var data = {
          match_id: item.match_id,
          user_id: this.userInfo.id,
          match_group_id: item.id
        }
        this.httpService.post('modifyMyMatchGroup', data, (res) => {
          console.log(res);
          if (res.statusCode == 0) {
            this.toastService.showMessage("info", res.msg);;
            this.getMyGame();
          }
          else {
            this.toastService.showMessage("error", res.msg);
          }
        });
      }
    });
  }


}
