import { Component, OnInit, ViewChild } from '@angular/core';
import { NzIconService } from 'ng-zorro-antd';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, NavigationExtras } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
import { AppConfig } from '../../appConfig';
import { DomSanitizer } from '@angular/platform-browser';
@Component({
  selector: 'app-zhuye',
  templateUrl: './zhuye.component.html',
  styleUrls: ['./zhuye.component.css']
})
export class ZhuyeComponent implements OnInit {
  // @ViewChild("adswiperChild") adswiperChild;
  newsList = [];
  notifyList = [];
  recentMatchList1 = [];
  recentMatchList2 = [];
  constructor(
    private _iconService: NzIconService,
    private fb: FormBuilder,
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
    public _d: DomSanitizer // 图片路径转换使用，使用见HMTL文件,防止xss
  ) {
    this._iconService.fetchFromIconfont({
      // scriptUrl: 'https://at.alicdn.com/t/font_8d5l8fzk5b87iudi.js'
      scriptUrl: 'https:////at.alicdn.com/t/font_1304347_v9bi6fabttp.js'
    });
  }

  ngOnInit() {
    this.init()
  }
  init() {
    this.getNewsAndNotify();
    this.getRecentMatch();
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.

  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    console.log("zhuye destroy");
    // this.adswiperChild.adSwiper.destroy(true, true);
  }

  //首页获取n条新闻n条通知
  getNewsAndNotify() {
    this.httpService.post('getNewsAndNotify', {}, (res) => {
      console.log(res);
      var data = res.data;
      if (res.statusCode == 0) {
        for (let i = 0; i < data.news.length; i++) {
          data.news[i].time = data.news[i].release_time.substr(0, 10);
        }
        for (let i = 0; i < data.notify.length; i++) {
          data.notify[i].time = data.notify[i].release_time.substr(0, 10);
        }
        this.newsList = data.news;
        this.notifyList = data.notify;
      }
    });
  }
  //首页获取8条近期赛事
  getRecentMatch() {
    this.httpService.post('getRecentMatch', {}, (res) => {
      console.log(res);
      var data = res.data;
      this.recentMatchList1.push(data[0], data[1], data[2], data[3]);
      this.recentMatchList2.push(data[4], data[5], data[6], data[7]);
    });
  }

  goNewsDetail(id) {
    let navigationExtras: NavigationExtras = {
      queryParams: { 'news_id': id },
      // fragment: 'anchor'
    };
    this.router.navigate(['/newsdetail'], navigationExtras);
  }



}
