import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpService } from '../../../providers/http.service';
import { ToastService } from '../../../providers/toast.service';
import { UtilService } from '../../../providers/util.service';

@Component({
  selector: 'app-modifypwd',
  templateUrl: './modifypwd.component.html',
  styleUrls: ['./modifypwd.component.css']
})
export class ModifypwdComponent implements OnInit {

  sendSmsText = "获取验证码";
  sendSmsActive = false;
  sendSmsTip = "";
  hasPhoneNum = true;
  showYZMdialog = true;
  showModifydialog = false;

  validateForm1: FormGroup;
  validateForm2: FormGroup;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
  ) { }

  ngOnInit() {
    this.initLoginForm1();
    this.initLoginForm2();
  }

  initLoginForm1() {
    this.validateForm1 = this.fb.group({
      phone2: [null, [Validators.required]],
      code: [null, [Validators.required]],
      // remember: [true]
    });
  }
  submitForm1(): void {
    console.log("提交忘记密码-验证码");
    for (const i in this.validateForm1.controls) {
      this.validateForm1.controls[i].markAsDirty();
      this.validateForm1.controls[i].updateValueAndValidity();
    }
    if (this.validateForm1.status == 'VALID') {
      var data = {
        phone: this.validateForm1.value.phone2,
        code: this.validateForm1.value.code,
      };
      // console.log(data);
      this.httpService.post('validatePhoneCode', data, (res) => {
        console.log(res);
        if (res.statusCode == 0) {
          this.showYZMdialog = false;
          this.showModifydialog = true;
        }
        else {
          this.toastService.showMessage("error", res.msg);
        }
      });
    }
  }

  initLoginForm2() {
    this.validateForm2 = this.fb.group({
      new_pwd: [null, [Validators.required]],
      confirm_pwd: [null, [Validators.required]],
      // remember: [true]
    });
  }
  //提交保存密码
  submitForm2(): void {
    console.log("提交忘记密码-设置新密码");
    for (const i in this.validateForm2.controls) {
      this.validateForm2.controls[i].markAsDirty();
      this.validateForm2.controls[i].updateValueAndValidity();
    }
    if (this.validateForm2.status == 'VALID') {
      if (this.validateForm2.value.new_pwd != this.validateForm2.value.confirm_pwd) {
        this.toastService.showMessage("error", "两次密码输入不一致");
        return;
      }
      var data = {
        phone: this.validateForm1.value.phone2,
        pwd: this.validateForm2.value.new_pwd
      }
      this.httpService.post('changePwd', data, (res) => {
        console.log(res);
        this.toastService.showMessage("info", res.msg);
        this.showYZMdialog = true;
        this.showModifydialog = false;
        this.utilService.delLS("userInfo");
        this.router.navigate(['/home']);
        this.utilService.LoginIsVisible = true;
      });
    }

  }

  //获取验证码
  getCaptcha(e) {
    e.preventDefault();
    console.log(this.validateForm1);
    //30s冷却发送验证码按钮
    var count = 30;
    this.sendSmsActive = true;
    var time = window.setInterval(() => {
      this.sendSmsText = count + "秒后再次发送";
      count--;
      if (count < 0) {
        window.clearInterval(time);
        this.sendSmsActive = false;
        this.sendSmsText = "获取验证码";
      }
    }, 1000);
    var data = {
      phone: this.validateForm1.value.phone2,
      type: 2 //获取忘记密码验证码
    }
    this.httpService.post('sendSMS', data, (res) => {
      console.log(res);
      this.sendSmsTip = res.msg;
      if (res.statusCode != 0) {
        this.hasPhoneNum = false;
      } else {
        this.hasPhoneNum = true;
      }
    });
  }

}
