import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
@Component({
  selector: 'app-matchlist',
  templateUrl: './matchlist.component.html',
  styleUrls: ['./matchlist.component.css']
})
export class MatchlistComponent implements OnInit {
  matchList = [];
  cityList = [];
  cityId = "0";
  userInfo: any;
  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.getGameList(this.cityId);
    this.getCity();
  }
  //判断是否有登录，是否有userinfo
  checkLogin() {
    this.userInfo = this.utilService.getLS("userInfo");
    if (!this.userInfo) {
      this.toastService.showMessage("info", "请您先登录");
      this.router.navigate(['/home']);
      this.utilService.LoginIsVisible = true;
    }
  }
  //获取比赛列表
  getGameList(cityId) {
    if (cityId == null || cityId == '' || cityId == undefined) {
      cityId = 0;
    }
    this.checkLogin();
    var param = {
      cityId: cityId,
      user_id: this.userInfo.id
    }

    this.httpService.post('getMatchList', param, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        this.matchList = data;
      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });

  }
  //获取城市下拉框
  getCity() {
    this.httpService.post('getCity', {}, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        this.cityList = data;
        // this.cityList.unshift({ id: 0, name: '全省' })
      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  cityChange(v) {
    console.log(v);
    this.getGameList(v);
  }
  goMatchDetail(id) {
    let navigationExtras: NavigationExtras = {
      queryParams: { 'match_id': id },
      // fragment: 'anchor'
    };
    this.router.navigate(['/matchdetail'], navigationExtras);
  }

}
