import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
@Component({
  selector: 'app-newslist',
  templateUrl: './newslist.component.html',
  styleUrls: ['./newslist.component.css']
})
export class NewslistComponent implements OnInit {
  list = [];
  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.getNewsList();
  }
  getNewsList() {
    this.httpService.post('getNewsList', {}, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        for (let i = 0; i < data.length; i++) {
          // var time = data[i].release_time;
          if (!data[i].author) {
            data[i].author = "福建省围棋协会";
          }
          if (data[i].author == "个人注册") {
            data[i].author = "系统管理员";
          }
          var dd = data[i].content.replace(/<\/?.+?>/g, "");
          data[i].content = dd.replace(/ /g, "");//dds为得到后的内容
          data[i].content = data[i].content.substr(0, 100) + "......";
          // if (time) {
          //   time = time.split(' ');
          //   time = time[0].split('-')
          // }
        }
        this.list = data;
      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  goNewsDetail(id) {
    let navigationExtras: NavigationExtras = {
      queryParams: { 'news_id': id },
      // fragment: 'anchor'
    };
    this.router.navigate(['/newsdetail'], navigationExtras);
  }

}
