import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
@Component({
  selector: 'app-matchdetail',
  templateUrl: './matchdetail.component.html',
  styleUrls: ['./matchdetail.component.css']
})
export class MatchdetailComponent implements OnInit {
  userInfo: any;
  match_id: any;
  selected_match: any;
  enlist_list = [];
  random_list = [];
  selected_group: any//要查看随机分组情况的赛段组
  // 对话框
  isVisible = false;
  isVisible1 = false;
  //赛规赛程-赛事排名切换按钮
  radioValue = "rule";
  //查询成绩的组别
  query_rank_group: any;
  matchScore_list = [];

  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.getGameDetails();
  }
  //判断是否有登录，是否有userinfo
  checkLogin() {
    this.userInfo = this.utilService.getLS("userInfo");
    if (!this.userInfo) {
      this.toastService.showMessage("info", "请您先登录");
      this.router.navigate(['/home']);
      this.utilService.LoginIsVisible = true;
    }
  }
  getGameDetails() {
    //rxjs(Observable)对象,订阅获取数据
    this.activatedRoute.queryParams.subscribe((data) => {
      console.log(data);
      this.match_id = data.match_id;
      if (data.match_id) {
        var param = {
          match_id: data.match_id
        }
        this.httpService.post('getMatchById', param, (res) => {
          console.log(res);
          if (res.statusCode == 0) {
            var data = res.data;
            this.selected_match = data;
            this.query_rank_group = this.selected_match.match_group[0];
            this.getGameRank(this.query_rank_group.id);//获取排名信息
          }
          else {
            this.toastService.showMessage("error", res.msg);
          }
        });
      }
    });
  }

  //查看报名选手
  queryEnlist_list() {
    this.isVisible = true;//打开对话框
    var match_id = this.match_id;
    var data = {
      match_id: match_id,
    }
    this.httpService.post('queryEnlist_list', data, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        this.enlist_list = data;
      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }
  //查看随机分组情况
  queryRandomGroup(match_group_id, selectGroup) {
    this.isVisible1 = true;
    this.selected_group = selectGroup;
    var data = {
      match_id: this.selected_match.id,
      match_group_id: match_group_id
    }
    this.httpService.post('queryRandom', data, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;
        this.random_list = data;
      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  //报名跳转扫码缴费页面
  signUp(match_group_id, group_price) {
    this.checkLogin();
    var data = {
      match_id: this.match_id,
      user_id: this.userInfo.id
    }
    this.httpService.post('doEnlist', data, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var haveWxAcc = 0;
        if (res.data.wx_acc != "" && res.data.wx_acc != null && res.data.wx_acc != undefined) {
          haveWxAcc = 1;
        }
        var order;
        if (this.selected_match.type == '段级位赛') {
          order = 3;
        } else if (this.selected_match.type == '1-3段位赛') {
          order = 4;
        } else if (this.selected_match.type == '4-6段位赛') {
          order = 5;
        } else {
          order = 6;
        }
        let navigationExtras: NavigationExtras = {
          queryParams: {
            'order': order,
            'match_id': match_group_id,//赛段组id
            'price': this.selected_match.price,
            'group_price': group_price,
            'haveWxAcc': haveWxAcc,
            'orgid': res.data.id,
          },
          // fragment: 'anchor'
        };
        this.router.navigate(['/person/pay'], navigationExtras);
      }
      else {
        this.toastService.showMessage("info", res.msg);
      }
    });
  }

  //分组查询排名change
  rankGroupChange(val) {
    console.log(val);
    this.getGameRank(val.id);
  }

  //获取赛事分数排名
  getGameRank(group_id) {
    var match_id = group_id;//传赛段组id
    var url = URL + 'getMatchScore';
    var data = {
      match_id: match_id,
    }
    this.httpService.post('getMatchScore', data, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        var data = res.data;

        this.matchScore_list = data.list;
        for (let i = 0; i < this.matchScore_list.length; i++) {
          this.matchScore_list[i].expand = false;
        }

      }
      else {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  handleOk(): void {
    console.log('Button ok clicked!');
    this.isVisible = false;
  }

  handleCancel(): void {
    console.log('Button cancel clicked!');
    this.isVisible = false;
  }

  handleOk1(): void {
    console.log('Button1 ok clicked!');
    this.isVisible1 = false;
  }

  handleCancel1(): void {
    console.log('Button1 cancel clicked!');
    this.isVisible1 = false;
  }

}
