import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
import { DomSanitizer } from '@angular/platform-browser';
@Component({
  selector: 'app-newsdetail',
  templateUrl: './newsdetail.component.html',
  styleUrls: ['./newsdetail.component.css',]
})
export class NewsdetailComponent implements OnInit {
  select_news: any;
  news_id: any;
  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
    private activatedRoute: ActivatedRoute,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.getNewsDetail();
  }
  getNewsDetail() {
    this.activatedRoute.queryParams.subscribe((data) => {
      console.log(data);
      this.news_id = data.news_id;
      if (data.news_id) {
        var param = {
          news_id: data.news_id
        }
        this.httpService.post('getNewsDetail', param, (res) => {
          console.log(res);
          if (res.statusCode == 0) {
            var data = res.data;
            this.select_news = data;
            // this.select_news.content = this.sanitizer.bypassSecurityTrustHtml(this.select_news.content);
          }
          else {
            this.toastService.showMessage("error", res.msg);
          }
        });
      }
    });
  }

}
