import { NgModule } from '@angular/core';
import { Routes, RouterModule, RouteReuseStrategy } from '@angular/router';
import { CustomReuseStrategy } from './customReuseStrategy';//路由重用策略
import { ZhuyeComponent } from './page/zhuye/zhuye.component';
import { MatchlistComponent } from './page/matchlist/matchlist.component';
import { NewslistComponent } from './page/newslist/newslist.component';
import { CurtureComponent } from './page/curture/curture.component';
import { RegisterComponent } from './page/register/register.component';
import { RegistersuccessComponent } from './page/registersuccess/registersuccess.component';
import { NotifylistComponent } from './page/notifylist/notifylist.component';
import { PersonComponent } from './page/person/person.component';
import { PayComponent } from './page/person/pay/pay.component';
import { EditinfoComponent } from './page/person/editinfo/editinfo.component';
import { ModifypwdComponent } from './page/person/modifypwd/modifypwd.component';
import { MymatchComponent } from './page/person/mymatch/mymatch.component';
import { MatchdetailComponent } from './page/matchdetail/matchdetail.component';
import { NewsdetailComponent } from './page/newsdetail/newsdetail.component';

const routes: Routes = [
  { path: "home", component: ZhuyeComponent },
  { path: "matchlist", component: MatchlistComponent },
  { path: "newslist", component: NewslistComponent },
  { path: "notifylist", component: NotifylistComponent },
  { path: "culture", component: CurtureComponent },
  { path: "register", component: RegisterComponent },
  { path: "registersuccess", component: RegistersuccessComponent },
  { path: "matchdetail", component: MatchdetailComponent },
  { path: "newsdetail", component: NewsdetailComponent },
  {
    path: "person", component: PersonComponent,
    children: [
      { path: "pay", component: PayComponent },
      { path: "edit", component: EditinfoComponent },
      { path: "modifypwd", component: ModifypwdComponent },
      { path: "mymatch", component: MymatchComponent },
      { path: "**", component: EditinfoComponent },
    ]
  },
  { path: '**', redirectTo: 'home', pathMatch: 'full' },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes,
      {
        onSameUrlNavigation: "reload",
        useHash: true
      })
  ],
  exports: [RouterModule],
  providers: [/* 配置路由策略 */
    // { provide: RouteReuseStrategy, useClass: CustomReuseStrategy }
  ]
})
export class AppRoutingModule { }
