import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpService } from '../../providers/http.service';
import { ToastService } from '../../providers/toast.service';
import { UtilService } from '../../providers/util.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  validateForm: FormGroup;
  validateForm1: FormGroup;
  validateForm2: FormGroup;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    public utilService: UtilService,

  ) { }
  isLogin = false;
  isOkLoading = false;
  isVisible1 = false;
  isOkLoading1 = false;
  loginBodyStyle = {
    padding: "12px 24px"
  };
  sendSmsText = "获取验证码";
  sendSmsActive = false;

  showYZMdialog = true;
  showModifydialog = false;

  ngOnInit() {
    this.initLoginForm();
    this.initLoginForm1();
    this.initLoginForm2();
  }

  initLoginForm() {
    this.validateForm = this.fb.group({
      phone: [null, [Validators.required]],
      pwd: [null, [Validators.required]],
      // remember: [true]
    });
  }
  //登录
  submitForm(): void {
    console.log("提交登录");
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }
    if (this.validateForm.status == 'VALID') {   /* 通过验证 */
      // console.log(this.validateForm.value);
      var data = {
        phone: this.validateForm.value.phone,
        pwd: this.validateForm.value.pwd,
      };
      // console.log(data);
      this.httpService.post('login', data, this.loginSucCb);
    }
  }
  loginSucCb = (res) => {
    console.log(res);

    if (res.statusCode == 0) {
      this.toastService.showMessage("success", res.msg);
      this.utilService.LoginIsVisible = false;
      this.utilService.setLS('userInfo', res.data);
      // localStorage.setItem('userInfo', JSON.stringify(res.data));
      // this.isLogin=true;
    } else {
      this.toastService.showMessage("error", res.msg);
    }
  }
  initLoginForm1() {
    this.validateForm1 = this.fb.group({
      phone2: [null, [Validators.required]],
      code: [null, [Validators.required]],
      // remember: [true]
    });
  }
  submitForm1(): void {
    console.log("提交忘记密码-验证码");
    for (const i in this.validateForm1.controls) {
      this.validateForm1.controls[i].markAsDirty();
      this.validateForm1.controls[i].updateValueAndValidity();
    }
    if (this.validateForm1.status == 'VALID') {
      var data = {
        phone: this.validateForm1.value.phone2,
        code: this.validateForm1.value.code,
      };
      // console.log(data);
      this.httpService.post('validatePhoneCode', data, (res) => {
        console.log(res);
        if (res.statusCode == 0) {
          this.showYZMdialog = false;
          this.showModifydialog = true;
        }
        else {
          this.toastService.showMessage("error", res.msg);
        }
      });
    }
  }

  initLoginForm2() {
    this.validateForm2 = this.fb.group({
      new_pwd: [null, [Validators.required]],
      confirm_pwd: [null, [Validators.required]],
      // remember: [true]
    });
  }
  //提交保存密码
  submitForm2(): void {
    console.log("提交忘记密码-设置新密码");
    for (const i in this.validateForm2.controls) {
      this.validateForm2.controls[i].markAsDirty();
      this.validateForm2.controls[i].updateValueAndValidity();
    }
    if (this.validateForm2.status == 'VALID') {
      if (this.validateForm2.value.new_pwd != this.validateForm2.value.confirm_pwd) {
        this.toastService.showMessage("error", "两次密码输入不一致");
        return;
      }
      var data = {
        phone: this.validateForm1.value.phone2,
        pwd: this.validateForm2.value.new_pwd
      }
      this.httpService.post('changePwd', data, (res) => {
        console.log(res);
        this.toastService.showMessage("info", res.msg);
        this.showYZMdialog = true;
        this.showModifydialog = false;
        this.isVisible1 = false;
        this.utilService.LoginIsVisible = true;

      });
    }

  }

  showModal(): void {
    this.utilService.LoginIsVisible = true;
  }
  showModal1(): void {
    this.utilService.LoginIsVisible = false;
    this.isVisible1 = true;
  }
  //跳转注册页
  goRegister() {
    console.log("跳转注册页");
    this.utilService.LoginIsVisible = false;
    this.router.navigate(['/register']);
  }
  //获取验证码
  getCaptcha(e) {
    e.preventDefault();
    console.log(this.validateForm1);
    //30s冷却发送验证码按钮
    var count = 30;
    this.sendSmsActive = true;
    var time = window.setInterval(() => {
      this.sendSmsText = count + "秒后再次发送";
      count--;
      if (count < 0) {
        window.clearInterval(time);
        this.sendSmsActive = false;
        this.sendSmsText = "获取验证码";
      }
    }, 1000);
    var data = {
      phone: this.validateForm1.value.phone2,
      type: 2 //获取忘记密码验证码
    }
    this.httpService.post('sendSMS', data, (res) => {
      console.log(res);
      if (res.statusCode != 0) {
        this.toastService.showMessage("error", res.msg);
      }
    });
  }

  handleOk(): void {
    this.isOkLoading = true;
    setTimeout(() => {
      this.utilService.LoginIsVisible = false;
      this.isOkLoading = false;
    }, 3000);
  }
  handleOk1() {
    console.log("ok1")
  }

  handleCancel(): void {
    this.utilService.LoginIsVisible = false;
  }
  handleCancel1(): void {
    this.isVisible1 = false;
  }

  loginOut() {
    console.log("退出");
    this.utilService.delLS("userInfo");
    this.router.navigate(['/home']);
  }

}
