import { Component, OnInit, ElementRef } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { HttpService } from '../../../providers/http.service';
import { ToastService } from '../../../providers/toast.service';
import { UtilService } from '../../../providers/util.service';
@Component({
  selector: 'app-pay',
  templateUrl: './pay.component.html',
  styleUrls: ['./pay.component.css']
})
export class PayComponent implements OnInit {
  pay_type = '1';
  orderData: any;
  userInfo: any;
  constructor(
    private router: Router,
    private httpService: HttpService,
    private toastService: ToastService,
    private utilService: UtilService,
    private activatedRoute: ActivatedRoute,
    private el: ElementRef
  ) { }

  ngOnInit() {
    this.init();
  }
  init() {
    this.generateOrder();
    // this.checkLogin();
  }
  //生成订单
  generateOrder() {
    //rxjs(Observable)对象,订阅获取数据
    this.activatedRoute.queryParams.subscribe((data) => {
      console.log(data);

      this.orderData = data;
    });
  }
  //判断是否有登录，是否有userinfo
  checkLogin() {
    this.userInfo = this.utilService.getLS("userInfo");
    if (!this.userInfo) {
      this.toastService.showMessage("info", "请您先登录");
      this.router.navigate(['/home']);
      this.utilService.LoginIsVisible = true;
    }
  }
  payFun() {
    this.checkLogin();
    //判断是否已经报名
    var param = {
      match_group_id: this.orderData.match_id,
      user_id: this.userInfo.id
    }
    this.httpService.post('queryIsEnlist_pay', param, (res) => {
      console.log(res);
      if (res.statusCode == 0) {
        this.toastService.showMessage("info", "您已报名此赛事，请勿重复报名");
      }
      else {//还未报名
        this.pay_type = this.orderData.haveWxAcc != '1' ? '1' : '2';

        this.el.nativeElement.querySelector('#user_id').value = this.userInfo.id;
        this.el.nativeElement.querySelector('#pay_id').value = this.orderData.match_id;
        this.el.nativeElement.querySelector('#orgid').value = this.orderData.orgid;
        this.el.nativeElement.querySelector('#pay_type').value = this.pay_type;
        console.log('paytype:' + this.pay_type);
        console.log(this.el.nativeElement.querySelector('#pay_type').value)
        this.el.nativeElement.querySelector('#formPay').submit();
      }
    });
  }

}
