import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UtilService {

  public islogin = false;//登录标志
  public LoginIsVisible = false;//登录窗开启关闭标志



  constructor() { }
  /**
   * 封装localstorage
   */
  public getLS(key) {
    return JSON.parse(localStorage.getItem(key));
  }
  public setLS(key, value) {
    localStorage.setItem(key, JSON.stringify(value));
  }
  public delLS(key) {
    localStorage.removeItem(key);
  }

  /**
   * 时间日期格式化
   */
  //获取时间"年-月-日"格式
  public getDate(now: Date = new Date()) {
    // var now = new Date(),
    var y = now.getFullYear();
    var m = now.getMonth() + 1;
    var d = now.getDate();
    return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
  }
  //获取时间"时:分:秒"格式
  public getTime(now: Date = new Date()) {
    var h = now.getHours();
    var m = now.getMinutes();
    var s = now.getSeconds();
    return (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m) + ":" + (s < 10 ? "0" + s : s);
  }
  //获取时间"年-月-日 时:分:秒"格式
  public getDateTime(now: Date = new Date()) {
    var y = now.getFullYear();
    var mo = now.getMonth() + 1;
    var d = now.getDate();
    var h = now.getHours();
    var m = now.getMinutes();
    var s = now.getSeconds();
    return y + "-" + (mo < 10 ? "0" + mo : mo) + "-" + (d < 10 ? "0" + d : d) + " " + (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m) + ":" + (s < 10 ? "0" + s : s);
  }



}
